
import XInput from 'vux/src/components/x-input/index.vue'

export default {
name: 'typing-screen',
components: {
XInput
},
data () {
return {
carno: '',
errorMsg: ''
}
},
methods: {

getUnlockCode () {
const {carno} = this

this.$loading.show()

this.errorMsg = ''

$API.ofo.getUnlockCode({carno}).then((result) => {
success(result)
}).catch((e) => {
this.$toast.text(e)
}).finally(() => {
this.$loading.hide()
})

const success = (result) => {
const {OK, code, msg, data} = result
if (OK) {

const {pwd} = data
this.$router.push({name: 'UnlockedCodeScreen', params: {pwd}})
} else {
this.errorMsg = msg
switch (code) {
case 30005: // 骑行中
this.$router.push({name: 'RidingScreen'})
break
case 40078: // 骑行结束
break
case 40003: // 未结单
break
case 30009: // 车辆停靠在非法区域
break
case 30004: // 没有此车辆
break
case 50000: // 非法区域
break
case 50001: // 服务器响应
break
default: // 结单不成功
}
}
}
}
},
mounted () {
this.$refs.ofoNumInput.focus()
}
}



